.container {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    gap: 0px 0px;
    grid-template-areas:
    ". . ."
    ". c ."
    ". . l";
}

.content {
    grid-area: c;
}

.loginInfo{
    grid-area: l;
}